.main__slide {
    background-color: #f3f3f3;
    height: 80vh;
    position: relative;
}

.slide__item{
    height: inherit;
    display: grid;
    grid-template-columns: 50% 50%;
}

.slide__item_info{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 60px;
    padding-top: 60px;
    width: 60%;
    margin-right: auto;
    margin-left: auto;
}

.slide__item_info h1 {
    display: none;
    color: #1f1f1f;
    text-transform: uppercase;
    letter-spacing: 1px;
    word-spacing: 5px;
    font-weight: 600;
    animation: mainHeader 0.7s ease-out;
}

.slide__item_info p {
    display: none;
    color: #b3b1b1;
    font-weight: 300;
    animation: mainHeader 0.7s ease-out 0.3s forwards;
        transform: translateX(80px);
        opacity: 0;
}

.slide__item_cover, .slide__content{
    height: inherit;
}
.slide__item_cover{
        padding-top: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}


.slide__item_cover img{ 
    display: none;
    height: inherit;
    animation: image 0.7s ease-out;

}


.slide__options {
    cursor: pointer;
    position: absolute;
    left: -45px;
    top: 45%;
    opacity: 0;
    animation: opa 0.7s ease-in 0.5s forwards;
    color: #b3b1b1;
}


.slide__option_item {
    position: relative;
    margin-bottom: 15px;
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    justify-content: flex-start;
}

.slide__option_item::after{
    content: "";
    width: 19.5px;
    height: 1.5px;
    left: 25px;
    position: absolute;
    padding: 0px !important;
    background-color: #b3b1b1;
}

.slide__number{
    font-weight: 600;
    letter-spacing: 2px;
    font-size: 16px;
}

.slide__active::after{
    background-color: #080808;
    width: 39px;
    animation: line 0.5s ease-out;
       
}

.slide__active>.slide__number{
    color: #080808;
}

.customProgres{
    position: absolute;
    left: 0;
    top: 0;
    transform-origin: left top;
    height: 100%;
    width: 100%;
    background-color: #080808;
}

@keyframes mainHeader {
    0%{
        opacity: 0;
        transform: translateX(80px);
    }
    95%{
        opacity: 1;
    }
    100%{
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes opa{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes description {
    0% {
        opacity: 0;
        transform: translateX(80px);
    }

    95% {
        opacity: 1;
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}


@keyframes image {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }

    95% {
        opacity: 1;
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}


@keyframes MobileImage {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }

    95% {
        opacity: 1;
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes line {
    0%{
        width: 0px;
            
    }
    100%{
        width: 39px;
        
    }
}


@media screen and (max-width:1024px) {
    .slide__options{
        display: none;
    }
}

@media screen and (max-width:792px) {
    .slide__item_cover{
        height: 90%;
    }
    .slide__item_info{
        width: 80%;
    }
}

@media screen and (max-width:600px) {
    .container {
        width: 100%;
    }

   
}


@media screen and (max-width:580px) {
    .slide__item {
        grid-template-columns: auto;
        grid-template-rows: 65% 35%;
        grid-template-areas: "cover""info"
    }

    .slide__item_info{
        grid-area: info;
        justify-content: flex-start;
        padding: 0;
    }

    .slide__item_info h1{
        font-size: 24px;
    }

    .slide__item_cover{
        position: relative;
        padding: 0;
        height: 300px;
    }

    .slide__item_cover img {
        animation: MobileImage 0.7s ease-out;
        height: 100%;
        object-fit: cover;
        object-position: center;
        
    }
    
}

@media screen and (max-width:320px) {
    .main__slide{
        height: 90vh;
    }

}