.products__list {
    margin-top: 5vw;
}



.products__content {
    width: 100%;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));;
}

.product__item {
    position: relative;
    background-color: #f3f3f3;
}

.product__item_header{
    position: absolute;
    top: 0;
    z-index: 555;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 13px;
    color: #515151;

}

.product__item_cover{
    padding-top: 15px;
    overflow: hidden;
    height: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.product__item_cover img{
    object-fit: contain;
    object-position: center;
    transition: all 0.3s ease-in-out;
    width: 230px;
    height: 230px;
}


.product__item_cover:hover >.product__item_cover_hover{
    height: 24px;
}

.product__item_cover_hover{
    position: absolute;
    bottom: 68px;
    left: calc(100px);
    width: 130px;
    font-size: 11px;
    text-align: center;
    display: flex;
    height: 0;
    transition: height 0.1s ease-out;
    text-transform: uppercase;
    color: white;
    overflow: hidden;
    animation: hoverBadge 0.1s ease-in;
}

.product__item_cover_hover>div{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    cursor: pointer;

}

.product__item_cover_hover>div:nth-child(1){
    background-color: #080808;
    height: 24px;
    width: 80px;
}

.product__item_cover_hover>div:nth-child(2) {
    background-color: #515151;
    width: 30px;
}

.product__item_info{
    padding: 20px 0px;
    text-align: center;
}

.product__item_label{
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 2px;
    margin-top: 10px;
    text-transform: uppercase;
}

.product__item_label a{
    text-decoration: none;
    color: #080808;
}

.product__item_prices,  .product__item_prices strike, .product__item_atc{
    font-family: "Montserrat";
    font-size: 13px;
    color: #999;
    word-spacing: 10px;
}

.product__item_prices{
    transition: all 0.3s ease-in;
}

.product__item_price>div{
    display: inline-block;
}

.product__item_atc{
    font-family: "Catamaran";
    text-transform: uppercase;
    opacity: 0;
    bottom: 23px;
    position: absolute;
    cursor: pointer;
    font-weight: 500;
    pointer-events: none;
    word-spacing: normal;    
    transition: all 0.4s ease-out;
    transform: translateX(0%);
    left: 0;
}


.product__item:hover .product__item_atc {
    pointer-events: all;
    opacity: 1;
    transform: translateX(115px);
    
}

.product__item:hover img {
    transform: scale(1.1)

}

.product__item:hover .product__item_prices {
    opacity: 0;
    transform: translateX(115px);

}

@keyframes slideIn {
    0%{
        opacity: 0;
        transform: translateX(-30%);
    }
    100%{
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes slideOut {
    0% {
        opacity: 1;
    }

    90% {
        transform: translateX(30%);
    }
    100% {
        opacity: 0;
    }
}