* {
    margin: 0;
    padding: 0;
    font-family: "Catamaran", sans-serif;
    box-sizing: border-box;
    outline: none;
}

.container {
    width: 86vw;
    margin-right: auto;
    margin-left: auto;
}

.container__label {
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 600;
    color: #080808;
    margin-bottom: 15px;
}

.container-fill {
    width: 86vw;
    margin-bottom: 50px;
    margin-right: auto;
    margin-left: auto;
}

::selection{
    background-color: #080808;
    color: white;
}


@media screen and (max-width:992px) {
    .container {
        width: 88vw;
    }
}

@media screen and (min-width:2000px) {
    .container, .container-fill {
        width: 60vw;
    }

}