.container_login{
    max-width: 400px;
    margin: 50px auto;
}

.tab__list{
    text-align: center;
    width: 100%;
    display: grid;
    align-items: center;
    grid-template-columns: auto  auto;
    border-bottom: 1px solid #bababa;
}

.tabPanel button{
    transition: all 0.3s;
}

.tabPanel button:disabled{
    background-color: #6d6d6d;
    cursor: default;
}

.def_input {
    margin: 15px 0;
}

.def_input label{
    display: block;
    font-size: 14px;
    letter-spacing: 0.5px;
    font-weight: 600;
    color: #585858;
}

.def_input input {
    width: 100%;
    padding: 10px 20px;
    border: 1px solid #dddddd;
}

.def_button{
    display: block;
    width: 100%;
    border: none;
    padding: 15px 0;
    background-color: #080808;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
}

.form__errors {
    background-color: rgb(255, 190, 190);
    color: rgb(164, 13, 13);
    padding: 10px 15px;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.7px;
    margin-top: 15px;
}

@media screen and (max-width:476px){
    .tabPanel {
        padding: 0 25px;
    }
}