.footer-content {
  margin-top: 50px;
  background-color: #080808;
  padding: 40px 30px;
}
.footer {
  display: grid;
  grid-template-columns: auto auto auto;
}

.footer-label {
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;

  color: #fff;
}

.footer a {
  margin-bottom: 4px;
  display: inline-block;
  font-size: 16px;
  color: #fff;
  text-decoration: none;
}

.footer ul {
  list-style: none;
}
.footer-block {
  width: fit-content;
}

.first-block {
  margin-right: auto;
}

.second-block {
  margin-right: auto;
  margin-left: auto;
}

.third-block {
  margin-left: auto;
}


@media screen and (max-width:770px) {
    .footer{
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
    }
    .third-blocks{
        grid-area: 2 / 1 / 2 / 3;
    }

    .third-block{
        margin-right: auto;
        margin-left: auto;
        margin-top: 20px;
    }
}

@media screen and (max-width:770px) {
    .footer {
        grid-template-columns: auto;
        grid-template-rows: auto auto auto;
    }

    .third-blocks {
        grid-area: 3 / 1 / 3 / 2;
    }
 
    .footer-block {
        margin-right: auto;
        margin-left: 0;
        margin-bottom:30px;
    }
}