.product__content {
  margin-top: 30px;
  margin-bottom: 60px;
  min-height: 500px;
  display: grid;
  grid-column-gap: 30px;
  grid-template-columns: 100px 600px auto;
}

.product__content > div {
  width: 100%;
  height: 100%;
}

.product__images {
  display: grid;
  grid-template-columns: auto;
  align-items: flex-start;
}

.product__images > div {
  width: 100%;
  height: 110px;
  overflow: hidden;
  background-color: #f3f3f3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.product__images > div > img {
  width: 100%;
  height: 110px;
  object-fit: cover;
  object-position: center;
  transform: scale(1);
  transition: all 0.3s;
}

.product__images > div > img:hover {
  transform: scale(1.1);
}

.product__cover {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: #f3f3f3;
}

.product__cover > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.product__info {
  padding-left: 25px;
}

.product__label {
  font-size: 24px;
  font-weight: bold;
  color: #080808;
}

.product__price {
  font-size: 24px;
  font-family: "Montserrat";
  color: #333;
  font-weight: 300;
}

.product__description {
  margin: 50px 0px;
  font-weight: 300;
  color: #828282;
}

.product__amount_form {
  display: flex;
  margin-bottom: 50px;
  align-items: center;
}

.product__amount_form_full {
  display: flex;
  margin-bottom: 50px;
  align-items: center;
}

.product__amount_border {
  display: flex;
  padding: 5px 0;
  padding-bottom: 6px;
  align-items: center;
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  border-left: 1px solid #e1e1e1;
}

.product__amount_border_full {
  display: flex;
  padding: 5px 0;
  padding-bottom: 6px;
  align-items: center;
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  border-left: 1px solid #e1e1e1;
}
.amount_form_label {
  font-size: 13px;
  color: #929292;
  font-weight: 300;
  letter-spacing: 1px;
  padding: 0 15px;
}

.amount_form_input {
  position: relative;
}

.amount_form_input .fa {
  position: absolute;
  color: #929292;
  top: 7px;
  cursor: pointer;
}

.amount_form_input .fa:nth-child(1) {
  left: 12px;
}

.amount_form_input .fa:last-child {
  right: 10px;
}

.amount_form_input input {
  width: 70px;
  font-family: "Montserrat";
  font-size: 16px;
  color: #929292;
  border: none;
  outline: none;
  padding: 5px 20px;
  text-align: center;
}

.amount_form_button {
  padding: 10px 40px;
  text-transform: uppercase;
  color: white;
  background-color: #080808;
  border: none;
  cursor: pointer;
  letter-spacing: 1px;
}

.amount_form_button_full {
  padding: 10px 40px;
  text-transform: uppercase;
  color: white;
  background-color: #080808;
  border: none;
  cursor: pointer;
  letter-spacing: 1px;
}

.product__little_info {
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 300;
}


.active_tab{
  background-color: #080808;
  color: white;
}

.tab{
  display: inline-block;
  list-style: none;
  cursor: pointer;
  padding: 10px 30px;
  transition: 0.3s all;
}

.tabs{
  margin-top: 30px;
}

.tab__label {
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 300;
  color: #080808;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.tab__text{
  text-align: justify;
  color: #929292;
  font-weight: 300;
}

.product__review{
  display: flex;
}

.review_details {
  padding-left: 20px;
}

.reviewer {
  color: #929292;
}

.review__text{
  font-weight: 300;
}

.review_form{
  margin: 15px 0px;
  max-width: 400px;
}

.review_form label{
  display: block;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 300;
  text-transform: capitalize;
}

.review_form input, .review_form select, .review_form textarea {
  width: 100%;
  padding: 5px 10px;
  border: #cccccc 1px solid;
}

.review_button {
  padding: 10px 40px;
  border: none;
  cursor: pointer;
  background-color: #080808;
  color: white;
  transition: 0.3s all;
}

.review_line {
  max-width: 400px;
  margin-top: 10px;
  border-top: 1px solid #ccc;
}

.review_button:hover{
  background-color: #333333;
}

@media screen and (max-width: 1280px) {
  .product__content {
    min-height: 600px;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 500px auto;
    grid-template-rows: auto;
  }

  .product__images {
    grid-area: 2 / 1 / 2 / 2;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 15px;
  }
}

@media screen and (max-width: 1024px) {
  .product__content {
    grid-template-columns: 50% auto;
    grid-template-rows: auto;
  }
}

@media screen and (max-width: 888px) {
  .product__content {
    grid-template-columns: auto;
    grid-template-rows: auto auto auto;
    grid-template-areas:
      "cover cover cover"
      "images images images"
      "info info info";
  }
  .product__cover {
    grid-area: cover;
    min-height: 60vw;
  }

  .product__images {
    grid-area: images;
    grid-template-columns: 50% 50%;
  }
  .product__info {
    grid-area: info;
    margin-bottom: 50px;
    padding-left: 0;
  }

  .product__images > div {
    width: 100%;
    height: 30vw;
  }

  .product__images > div > img {
    height: 100%;
  }

  .amount_form_button {
    padding: 10px 10px;
    font-size: 12px;
  }
}
