.header {
  display: grid;
  grid-template-columns: 40% auto 40%;
  justify-content: space-between;
  background-color: white;
  align-items: center;
}

.menu__item {
  text-decoration: none;
  font-weight: 600;
  color: #080808;
  font-size: 12px;
  text-transform: uppercase;
  display: inline-block;
  padding: 30px 20px;
}

.menu__utils {
  display: flex;
  justify-content: flex-end;
}

.menu__item > a {
  text-decoration: none;
  font-weight: 600;
  color: #080808;
  font-size: 12px;
  text-transform: uppercase;
  display: inline-block;
}

.dropdown__menu {
  position: relative;
}

.dropdown {
  position: absolute;
  z-index: 2;
  overflow: hidden;
  min-width: 150%;
  background-color: #080808;
  max-height: 0;
  padding: 0px 20px;
  top: 75px;
  opacity: 0;
  transition: all 0.5s ease;
}

.cart__dropdown > .dropdown {
  width: 240px;
}

.dropdown__menu:hover .dropdown {
  max-height: 500px;
  opacity: 1;
  padding: 20px;
}

.dropdown__left {
  left: 0;
  text-align: left;
}

.dropdown__right {
  right: 0;
  text-align: left;
}

.dropdown__item {
  cursor: pointer;
  display: block;
  text-decoration: none;
  color: white;
}

.dropdown__item_label {
  font-size: 12px;
  color: white;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.dropdown__item {
  text-transform: capitalize;
  margin-bottom: 5px;
  color: #a5a5a5;
  font-weight: 400;
}

.dropdown__cart_item {
  display: grid;
  margin-bottom: 10px;
  grid-template-columns: 30% 70%;
}

.dropdown__cart_item:last-child {
  margin-bottom: 0;
}

.dropdown__cart_item>div{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  height: 100px;
}

.dropdown__cart_item >div> img {
  height: 70px;
  width: 70px;
  object-fit: contain;
}

.cart__item_info {
  padding-top: 10px;
  position: relative;
  cursor: default;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 15px;
}

.cart__item_link {
  text-decoration: none;
  font-weight: 400;
  letter-spacing: 1px;
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 5px;
  color: white;
}

.cart__item_close {
  cursor: pointer;
  position: absolute;
  z-index: 2;
  right: -5px;
  font-weight: 200;
  font-size: 15px;
  top: 5px;
}

.cart__item_amount {
  font-size: 11px;
  color: #a5a5a5;
  font-family: sans-serif;
  font-weight: 300;
  margin-top: 5px;
}

.brand {
  font-size: 28px;
  font-weight: 700;
  color: #080808;
}

.mobile__menu {
  display: none;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 2px;
  color: #080808;
  font-weight: 600;
}

.menu__item.active {
  color: #929292;
}

.menu__item_sub {
  color: #929292;
  font-size: 13px;
}

.menu__item:hover {
  color: #929292;
}

.menu__item:hover a {
  color: #929292;
}

.menu__item_last:hover {
  color: #080808;
}

.menu__item_last > .fa:hover {
  color: #929292;
}

.menu__item .fa {
  padding-right: 5px;
  font-size: 14px;
  font-weight: 400;
}

.menu__item_last .fa {
  margin-left: 10px;
}

.sideMenu {
  position: absolute;
  z-index: 3;
  right: 0;
  top: 0;
  height: 100vh;
  min-width: 30vw;
  background-color: #080808;
  display: none;
  color: white;
  text-align: center;
  opacity: 0;
  animation: close 0.4s forwards;
}

.sideMenu a {
  color: white;
}

.sideMenu .dropdown__menu{
  padding: 0;
  padding-top: 15px;
}

.sideMenu .dropdown{
  top: 40px;
  left: -20px;
}

.sideMenu__close i{
  font-size: 14px;
  padding: 5px 7px;
  border: 3px solid white;
  border-radius: 50%;
  position: absolute;
  right: 10vw;
  top: 20px;
  cursor: pointer;
}

.sideMenu__label {
  font-size: 24px;
  margin: 15px 0;
}

.sideMenu form {
  display: grid;
  grid-template-columns: 70% 30%;
  width: 80vw;
  margin-right: auto;
  margin-left: auto;
}

.sideMenu input {
  padding: 10px 20px;
  background-color: transparent;
  border: 1px solid white;
  border-right: none;
  border-top-left-radius: 30px;border-bottom-left-radius: 30px;
}
.sideMenu button{
  background-color: #e1e1e1;
  border: none;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
  border-top-right-radius: 30px;border-bottom-right-radius: 30px;
}

@keyframes close {
  0%{
    opacity: 0;
  }

  100%{
    opacity: 1;
  }
}

@media screen and (max-width: 1024px) {
  .header {
    padding: 30px 0;
    grid-template-columns: auto auto;
    justify-content: space-between;
  }
  .menu,
  .menu__utils {
    display: none;
  }

  .mobile__menu {
    display: block;
  }

  .brand,
  .mobile__menu {
    margin: 0 15px;
  }
}

@media screen and (max-width: 768px) {
  .sideMenu {
    width: 50vw;
  }
  .brand,
  .mobile__menu {
    margin: 0 35px;
  }
}

@media screen and (max-width: 576px) {
  .sideMenu {
    width: 100vw;
  }
}
