.carts {
  padding: 0 20px;
}

.cart__card{
  width: 100%;
  background-color: #f3f3f3;
  min-height: 250px;
  max-height: 350px;
  margin: 30px 0px;
  display: grid;
  grid-template-columns: 20vw 20vw 10vw 10vw 10vw 10vw;
  align-items: center;
}

.card__cover{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 100%;
  height: 200px;
}

.card__cover>img {
  width: 100%;
  height: 15vw;
  object-fit: contain;
  transform: scale(0.9);
  transition: 0.2s ease-in;
}

.card__cover:hover img {
  transform: scale(1);
}

.card__title{
  font-size: 28px;
  font-weight: 700;
  color: #3a3a3a;
  line-height: 35px;
}

.card__price, .card__qty, .card__total, .card__close {
  text-align: center;
  font-size: 18px;
  font-family: "Montserrat";
}

.card__close i {
  color: rgb(254, 35, 79);
  font-size: 24px;
  cursor: pointer;
}

.card__close i:hover{
  color: rgb(212, 28, 65);
}

.card__qty{
  display: flex;
  flex-direction: row;
}
.card__qty button{
  font-size: 24px;
  width: 40px;
  height: 40px;
  border: 0.5px solid #3a3a3a;
  cursor: pointer;
  color: #3a3a3a;
  font-weight: 700;
  border-radius: 50%;
}

.card__qty input {
  text-align: center;
  background-color: transparent;
  border: none;
  font-size: 18px;
  font-family: "Montserrat";
  font-weight: 600;
  color: #3a3a3a;
  max-width: 30px;
}

@media screen and (max-width:790px) {
  .cart__card {
    grid-template-columns: auto auto auto auto auto;
    grid-template-rows: 200px auto;
    grid-template-areas: "pic pic pic pic pic"
    "title price qty total close";
    padding: 20px;
  }

  .card__cover{
    grid-area: pic;
  }
  .card__cover img {
    width: 100%;
    height: 100%;
  }
  .card__title{
    grid-area: title;
  }
  .card__qty {
    grid-area: qty;
  }
  .card__total {
    grid-area: total;
  }
    .card__close {
      grid-area: close;
    }
}


@media screen and (max-width:680px) {
  .cart__card {
    grid-template-columns: auto auto auto auto;
    grid-template-rows: 180px auto auto;
    grid-template-areas: "pic pic pic pic"
    "title title title title"
    "price qty total close";
    padding: 20px;
  }

  .card__title {
    grid-area: title;
    text-align: center;
    margin-bottom: 15px;
  }

}

@media screen and (max-width:580px) {
  .carts{
    padding: 0 15px;
  }
  .cart__card {
  max-height: 450px;
    grid-template-columns: auto auto;
    grid-template-rows: 180px auto auto auto;
    grid-template-areas: "pic pic"
      "title title"
      "price qty"
      "total close";
    padding: 20px;
  }

  .card__title {
    grid-area: title;
    text-align: center;
    margin-bottom: 15px;
  }

  .card__close, .card__price, .card__qty, .card__total{
    padding: 15px 0px;
  }

}
